<template>
  <div class="selected-opt--wrap">
    <h3 class="sub-header"> :</h3>
    <table class="selected-opt">
      <tr class="selected-opt--tr">
        <th class="selected-opt--td selected-opt--th">  :</th>
        <th class="selected-opt--td selected-opt--th">{{transExtraHeader}}:</th>
        <th class="selected-opt--td selected-opt--th">  :</th>
        <th class="selected-opt--td selected-opt--th"> :</th>
        <th class="selected-opt--td selected-opt--th">   ?:</th>
        <th class="selected-opt--td selected-opt--th"> :</th>
        <th class="selected-opt--td selected-opt--th">  :</th>
        <th class="selected-opt--td selected-opt--th">  :</th>
        <th class="selected-opt--td selected-opt--th">   :</th>
      </tr>
      <tr class="selected-opt--tr">
        <td class="selected-opt--td">{{transportData.trans.type}}</td>
        <td class="selected-opt--td">{{transExtraTitle}}</td>
        <td class="selected-opt--td">
          {{transportData.place.foreign || transportData.place.city || transportData.place.code}}
        </td>
        <td class="selected-opt--td">{{transportData.isLegalEntity ? " " : " "}}</td>
        <td class="selected-opt--td">{{transportData.taxi ? "" : ""}}</td>
        <td class="selected-opt--td">{{transportData.period.term}}</td>
        <td class="selected-opt--td">{{transportData.dtp ? transportData.dtp : "  "}}</td>
        <td class="selected-opt--td">{{transportData.privilege ? transportData.privilege : ""}}</td>
        <td class="selected-opt--td">{{transportData.osagoLimits}}</td>
      </tr>
    </table>
    <router-link class="router--link" to="/"> </router-link>
  </div>
</template>

<script>
  import {mapGetters} from "vuex";
  import {mapActions} from "vuex";

  export default {
    data() {
      return {}
    },
    computed: {
      ...mapGetters("transport", {
        "transExtraHeader": "transExtraHeader",
        "transExtraTitle": "transExtraTitle"
      }),
      ...mapGetters([
        "transportData"
      ])
    }
  }
</script>

<style lang="less">
  .selected-opt {
    display: block;

    &--wrap {
      font-size: 19px;
      margin-bottom: 40px;
      @media only screen and (max-width: 1195px) {
        font: inherit;
      }
    }

    &--tr {
      display: inline-block;
      margin-right: 5px;
      margin-top: 10px;
      margin-bottom: 15px;
    }

    &--th {
      font-weight: bold;
    }

    &--td {
      display: block;
      margin-bottom: 7px;
    }
  }
</style>




